import React from 'react';

class Stoks extends React.Component {
    render () {
        const imgStyle = {
            'minHeight': '250px',
        };

        return (
            <section >
                <div className="stak__picture">
                    <img style={imgStyle} src='../../../../img/banner3.jpg' alt="stocks"></img>
                    <h1 className="stak__picture__text"> </h1>
                </div>
                <ul className="additional__staks__list">
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>   </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div> -  10%   </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>   .   ,<br></br>, -  </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>    ,   <br></br>  </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div> 20%    </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>     10%<br></br>  </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>
                           : <br></br> ,   , -, , 
                    </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>
                        -,   11:00  17:00:  <br></br>  ,   , 
                     ,<br></br>     10%,  ,<br></br>    
                    </li>
                    <li className="additional__staks__list__item"><div className="staks__list__item__border"></div>- Fixprice 70/ 100</li>
                </ul>
            </section>
        ) ;
    }
}

export default Stoks;