import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

class Menu extends React.Component {
    constructor(props) {
        super(props);
        
    }

    scrollUp () {
        window.scrollTo(pageXOffset, 0);
    }

    render () {
        const {scrollUp} = this;
        return (
            <section className="menu" id="menu">
                <div className="menu__wrapper">
                    <div className="menu__info">
                        <h2 className="menu__title"> &nbsp; </h2>
                        <p className="menu__text">
                 . &nbsp;       .
                        </p>
                        <Link className="menu__btn btn" to='/menu' onClick={scrollUp} > </Link>
                    </div>
                    <div className="menu__back">
                        <div className="menu__list menu__carousel popup-gallery owl-carousel">
                            <a href="img/menu/bbq.jpg" title="  ">
                                <img src='../../../img/menu/bbq.jpg' alt=" "></img>
                            </a>
                            <a href="img/menu/first.jpg" title=" ">
                                <img src='../../../img/menu/first.jpg' alt=" "></img>
                            </a>
                            <a href="img/menu/second.jpg" title=" ">
                                <img src='../../../img/menu/second.jpg' alt=" "></img>
                            </a>
                            <a href="img/menu/salad.jpg" title="">
                                <img src='../../../img/menu/salad.jpg' alt=""></img>
                            </a>
                            <a href="img/menu/garnir.jpg" title="">
                                <img src='../../../img/menu/garnir.jpg' alt=""></img>
                            </a>
                            <a href="img/menu/snacks.jpg" title="">
                                <img src='../../../img/menu/snacks.jpg' alt=""></img>
                            </a>
                            <a href="img/menu/drink.jpg" title="">
                                <img src='../../../img/menu/drink.jpg' alt=""></img>
                            </a>
                            <a href="img/menu/beer.jpg" title=" ">
                                <img src='../../../img/menu/beer.jpg' alt=" "></img>
                            </a>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

Menu.propTypes = {
    change: PropTypes.func
};

export default Menu;